* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  background-color: #e6ecec;
}

.heading {
  text-align: center;
  background-color: black;
  padding: 30px 0px;

  width: 100vw;
  color: #e50914;
  font-size: 22px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}
.cards {
  width: 100%;
  height: auto;
}
.card {
  margin: 72px;
  background-color: #fff;
  width: 15em;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px;
  float: left;
  padding: 4px;
  transition: all 1s;
}
.card:hover {
  transform: scale(1.1);
}

.card_img {
  width: 100%;
  height: 235px;
  border-top-right-radius: 12px;
  border-top-left-radius: 12px;
}
.card_info {
  padding: 6px 24px 24px 24px;
}
button {
  text-indent: 5px;
  padding: 2px 4px 2px 2px;
  margin-top: 7px;
  outline: none;
  border: 1px solid black;
  font-weight: 500px;
  cursor: pointer;
}
button:hover {
  color: red;
}
